@import 'node_modules/bulma/bulma';
@import 'node_modules/font-awesome/scss/font-awesome';
@import 'node_modules/@fengyuanchen/datepicker/src/css/_datepicker';

$main-bg:#335a7d;
$green:#008571;
$blue:#7cc7ff;
$blue-light:#e2ebf5;
$red:#d74108;

body, html { 
    width: 100%;
    height: 100%;
}
aside {
	height: 100vh;
}
.is-space-between {
	justify-content: space-between;
}
.column-main,.column-aside {
	padding-right: 0;
	padding-left: 0;
}
.navbar {
	background:none;
}

.portrait {
	display: flex;
	justify-content:center;
	align-items:center;
	margin-top: 33px;
}

.nav {
	flex-direction:column;
	.nav-element {
		width:100%;
		margin-bottom: 40px;
		.nav-element-link {
			display:block;
			text-align: center;
			border:0;
			.icon {
				display:block;
				margin-left: auto;
				margin-right: auto;
			}
		}
	}
}

.column-aside {
	background: $main-bg;
}

.tabs {
	margin-top:40px;
}

.fa-bar-chart,.fa-file-o,.fa-cog {
  font-size:30px;
  color:$green;
}

.icon-text {
	color:$green;
}

header {
	background:$blue-light;
	padding-top:1px;
}
.tab-first {
	.field {
		margin-top: 45px;
	}
}
.direct-money {
	display:inline-block;
	&:first-child {
  	  margin-left: 20px;
  	  margin-right: 18px;
   }
}
.direct-money > input {
  visibility: hidden;
  position: absolute;
}

.direct-money > input + img {
	width: 40px;
	height: 40px;
	cursor:pointer;
	border:2px solid transparent;
}

.direct-money > input:checked + img {
	border:1px solid blue;
}
.content-tab-center {
  display:none;
}

.tab-content {
  display:none;
}

.show {
	display:block;
}

.tab-first {
	padding:15px;
}

.balance-account {
	display: flex;
    flex-basis: 25%;
    justify-content: space-around;
    margin-top: 30px;
}
.balance-tab {
	background:#e2ebf5;
	padding:25px;
	text-align:center;
}

.balance-title {
	display:block;
	font-size: 14px;
    font-weight: 600;
    margin-bottom: .3em;
}

.balance-price {
	line-height: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 24px
}

.table {
	width: 100%;
}

.settings-wrap {
	margin-top:90px;
}

.transaction-wrapper {
	margin-left:50px;
}